import React, { useState, useEffect, useContext } from "react";
import queryString from 'query-string'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    useLocation,
    useHistory,
    Link
} from "react-router-dom";
import { Button, Alert, Nav, Form, Col, InputGroup, Row, FormControl, Container, Table, Image, Card } from 'react-bootstrap'
import { baseUrl } from '../config'
import { AuthContext } from "../contexts";
import { toast } from 'react-toastify';
import useRWD from '../useRWD';

function Home(props) {
    const device = useRWD();
    const { search } = useLocation()
    const urlparams = queryString.parse(search)
    const history = useHistory();
    const [init, setinit] = useState(false)
    const [query, setQuery] = useState("")
    const { user, setUser } = useContext(AuthContext);

    function toLoginPage(){
        if(user === ""){
            toast(" let's login!", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: false,
                progress: undefined,
            });
            setTimeout(() => {
                history.push('/login')
            }, 3000)
        }
        else{
            toast(" enjoy your tripbook!", {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: false,
                draggable: false,
                progress: undefined,
            });
            setTimeout(() => {
                history.push('/poi')
            }, 3000)
        }
    }

    return (
        <>
            <br></br>
            <h2 style={{color:"#354458",fontFamily:"Microsoft JhengHei"}}> 
                {(device==="PC") ? <p></p> : ((device==="tablet") ? <p></p> : <p></p>)}
            </h2>  
            <div className="App" style={{ height:"10%", width:"100%"}}>
                {
                    (user)?<h4>Welcome back! {user}</h4>:<h4>Welcome to Tripbook@NTHU</h4>
                }
                <h6 className="App">ready to explore?</h6>
                <br></br>
            </div>
            <Card  style={{ borderColor:"white", opacity:"0.9"}}>
                <Card.Img variant="top" className="photo" src={'https://nthu-tripbook-assets.s3.ap-northeast-1.amazonaws.com/others/nthu-gate.jpg'} style={{ height:"100%", width:"100%"}} />
                <Card.Body>
                    <Card.Text>
                        <br></br><br></br>
                        <br></br><br></br>
                        
                    </Card.Text>
                </Card.Body>
            </Card>
            <Container>
                <Row>
                    <Col></Col>
                    <Col>
                        <Button className="w-100" 
                            style={{backgroundColor:'transparent', borderColor:'transparent'}}
                            onClick={() => toLoginPage()}
                        >
                            <Image variant="top" src={require(`../images/uis/main_button.png`)} style={{ height:"100%", width:"100%"}}/>
                        </Button>
                    </Col>
                    <Col></Col>
                </Row>
            </Container>

        </>
    );
}

export default Home;
